@mixin hover($_property, $_toValue) {
  transition: #{$_property} 0.5s;
  &:hover {
    #{$_property}: $_toValue;
  }
}

@mixin arrow-button($_bg-color, $_bg-size, $_icon-size, $_icon-img) {
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;

  background-color: $_bg-color;
  width: $_bg-size;
  height: $_bg-size;

  &::after {
    content: "";
    display: block;

    height: $_icon-size;
    width: $_icon-size;

    background-size: contain;
    background-repeat: no-repeat;
    background-image: url(#{$_icon-img});
  }
}

